* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Segoe UI', 'Roboto', 'Oxygen', sans-serif;
}

.App {
  position: relative;
  min-height: 100vh;
}

body {
  margin: 0;
  background-color: #F9FAFB;
}

.wrapper {
  padding: 6rem 0;
  margin: 0 auto;
  max-width: 30rem;
  width: 90%;
}

.dropdown-container,
.dropdown-list {
  border-radius: 4px;
}

.dropdown-container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 1rem;
  background-color: #fff;
  margin-bottom: .5rem;
  border: 1px solid #E5E7EB;
}

.dropdown__title {
  color: #475569;
}

.dropdown__toggle-btn {
  padding: .5rem;
  background: none;
  border: none;
  text-transform: uppercase;
  font-weight: 700;
  font-size: .875rem;
  color: #374151;
  cursor: pointer;
}

.dropdown-list {
  list-style: none;
  background-color: #fff;
  box-shadow: 0 5px 8px rgba(0, 0, 0, .15);
}

.dropdown-list__item {
  display: flex;
  justify-content: space-between;
  padding: 1rem;
  cursor: pointer;
}

.dropdown-list__item:not(:last-of-type) {
  border-bottom: 1px solid #E5E7EB;
}

.dropdown-list__item:hover {
  background-color: #F3F4F6;
}

.dropdown-list__item__selected-status {
  font-weight: 700;
  color: #374151;
}

.check-icon {
  width: 24px;
  height: 24px;
  stroke: #64748B;
}

.check-icon--hide {
  opacity: 0;
  pointer-events: none;
}